.SettingBackgroundImg{
display: flex;
flex-direction: column;
gap: 8px;
}
.SettingBackgroundImgBing{    
    color: var(--fontColor);
    cursor: pointer;
}
.dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: rgba(134, 196, 248, 1);
}
.SettingBackgroundImg>div,.SettingBackgroundImg>form{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 8px;
}